export function loadTenant(isSE: boolean): string | undefined {
    if (isSE) {
        // Cannot determine the tenant in SE.
        return undefined;
    }

    // If not SE, extract tenant from URL
    const path = window.location.pathname;
    if (path) {
        const parts = path.split('/');
        if (parts.length > 0) {
            return parts.pop() || '';
        }
    }
    return undefined;
}
