import { Telemetry } from '@splunk/skinny-web-telemetry';
import { createContext, useContext } from 'react';

export const TelemetryContext = createContext<TelemetryContextProps>({});

export const TelemetryContextProvider = TelemetryContext.Provider;

export const useTelemetry = (): Telemetry | undefined => {
    return useContext(TelemetryContext).telemetry;
};

export interface TelemetryContextProps {
    telemetry?: Telemetry;
}
