import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import React, { FC, useState } from 'react';
import { createDocsURL } from '@splunk/splunk-utils/url';
import { useInterval } from '../../../common/hooks/useInterval';
import { AssistStatuses, useAssist } from '../../../common/providers/Assist/AssistContext';
import { TenantStatus, TenantStatusState } from '../../../common/types';
import {
    AssistPendingContainer,
    StyledHeading,
    Subheading,
    ContactSupportLinkStyle,
} from './AssistPendingActivation.styles';
import {
    optInActivationDetails,
    PendingActivationState,
    getPendingActivationDetails,
} from './AssistPendingDetails';

export interface AssistPendingActivationProps {
    reloadAssistContext: () => void;
}

// This is state machine to determine what the current pending activation state is.
export function getPendingActivationState(
    optinStatus: AssistStatuses,
    tenantStatus: TenantStatus,
    assistEnabled: boolean
): PendingActivationState {
    if (!assistEnabled) {
        if (tenantStatus === TenantStatusState.UNKNOWN) {
            if (optinStatus === AssistStatuses.ENABLED) {
                return PendingActivationState.OPT_IN;
            }
            return PendingActivationState.INITIAL;
        }
        if (
            (tenantStatus === TenantStatusState.CREATING || tenantStatus === TenantStatusState.ACTIVE) &&
            optinStatus === AssistStatuses.ENABLED
        ) {
            return PendingActivationState.TENANT_CREATING;
        }
    }
    if (tenantStatus === TenantStatusState.ACTIVE && assistEnabled) {
        return PendingActivationState.COMPLETED;
    }
    return PendingActivationState.OPT_IN;
}

/**
 * Pending activation can take some time so we are updating the UI with different
 * messages so that user knows that things are happening. This code will rotate
 * messages from AssistPendingOptinDetails file while commerce is creating and
 * activating the tenant. Once tenant status changes to "creating", anything after
 * that, that is not "active", will be treated like an error.
 */
export const AssistPendingActivation: FC<AssistPendingActivationProps> = ({ reloadAssistContext }) => {
    const requestOptInStatusTimer = 5000; // 5 seconds
    const rotateMessageTimer = 20000; // 15 seconds
    const timeOutTimer = 180000; // 3 minute timer for inactivity

    const [currentIndex, setCurrentIndex] = useState(0);
    const [isActivationDelayed, setActivationDelayed] = useState(false);

    // Get the current pending activation values
    const { optinStatus, tenantStatus, assistEnabled } = useAssist();

    // Using the current activation values, find activation state
    const activationState = getPendingActivationState(optinStatus, tenantStatus, assistEnabled);

    // Gets the current message to displayed based on activation state
    const pendingStateDetails = getPendingActivationDetails(activationState, currentIndex);

    // reload page at an interval to see if there is an update
    useInterval(reloadAssistContext, requestOptInStatusTimer);

    // While the user is waiting for tenant_status = "creating", we will show a different messages
    // that will make it look like the activation is busy. This interval code with
    // increment an index as long as the activate state is OPT_IN.
    useInterval(() => {
        if (
            activationState === PendingActivationState.OPT_IN &&
            currentIndex < optInActivationDetails.length
        ) {
            setCurrentIndex(currentIndex + 1);
        }
    }, rotateMessageTimer);

    // This is a timer that once triggered, we will show a contact support message since
    // the activation is taking longer than expected.
    useInterval(() => {
        setActivationDelayed(true);
    }, timeOutTimer);

    return (
        <AssistPendingContainer>
            <WaitSpinner size="large" />
            <StyledHeading level={2} style={{ color: 'white' }}>
                {pendingStateDetails?.title}
            </StyledHeading>
            <Subheading level={4}>{pendingStateDetails?.message}</Subheading>
            <Subheading level={4}>{pendingStateDetails?.altMessage}</Subheading>
            {isActivationDelayed && (
                <>
                    <br />
                    <Subheading level={4}>
                        It looks like the activation is taking longer than expected.
                    </Subheading>
                    <Subheading level={4}>
                        If this persists,{' '}
                        <ContactSupportLinkStyle
                            to={createDocsURL('learnmore.splunkassist.overview')}
                            openInNewContext
                        >
                            contact support
                        </ContactSupportLinkStyle>
                    </Subheading>
                </>
            )}
        </AssistPendingContainer>
    );
};
