import { createHttpSupervisorService } from './httpSupervisorService';
import { APIClient } from '../api/APIClient';
import { Services } from '../../ui/common/providers/ServiceProvider';
import { createHTTPLicenserService } from './httpLicencerService';
import { createHTTPConfService } from './httpConfService';
import { createHTTPAccessService } from './httpAccessService';

export const createHttpServices = (): Services => {
    const apiClient = new APIClient({ shouldUseSCS: false });
    return {
        supervisorService: createHttpSupervisorService(apiClient),
        licenserService: createHTTPLicenserService(apiClient),
        confService: createHTTPConfService(apiClient),
        accessService: createHTTPAccessService(apiClient),
    };
};
