import { APIClient } from '../api/APIClient';
import { AccessService } from '../../ui/common/services/accessService';

/* eslint-disable camelcase */
export function createHTTPAccessService(apiClient: APIClient): AccessService {
    return {
        getCurrentContext: async () => {
            try {
                const response = await apiClient.get('getCurrentContext', {
                    method: 'GET',
                    path: 'authentication/current-context',
                    queryParams: {
                        output_mode: 'json',
                    },
                });
                const { content } = response.entry[0];

                // retrieve user name and email
                return { username: content?.username, email: content?.email };
            } catch {
                return null;
            }
        },
    } as AccessService;
}
