import { RequestError } from '../../ui/common/repos/RequestError';
import { SupervisorService } from '../../ui/common/services/supervisorService';
import { APIClient } from '../api/APIClient';

export function createHttpSupervisorService(apiClient: APIClient): SupervisorService {
    return {
        activate: async (code, licenseId) => {
            try {
                if (!licenseId) {
                    throw new RequestError('POST', 'activate', 'assist/onboard', 404);
                }
                const response = await apiClient.post('activate', {
                    method: 'POST',
                    path: 'assist/onboard',
                    body: {
                        code,
                        license_id: licenseId,
                    },
                });
                if (response.status === 'creating' || response.status === 'active') {
                    return {
                        message: 'Your account was successfully linked with your deployment',
                        valid: true,
                    };
                }
            } catch (error) {
                if (error instanceof RequestError) {
                    return {
                        code: error.status,
                        valid: false,
                    };
                }
            }
            return {
                code: null,
                valid: false,
            };
        },
        getIsAssistEnabled: async () => {
            const response = await apiClient.get('getOptinStatus', {
                method: 'GET',
                path: 'assist/optin_status',
            });
            return {
                connectivityOpen: response.context?.network_connectivity,
                optinStatus: response.context?.assist_optin,
                tenantStatus: response.context?.tenant_status,
                assistEnabled: response.assist_enabled,
                sudOptinStatus: response.context?.sud_optin,
            };
        },
    } as SupervisorService;
}
