import React, { createContext, FC, useContext, useMemo } from 'react';
import { createHttpServices } from '../../../http/services';
import { SupervisorService } from '../services/supervisorService';
import { LicenserService } from '../services/licencerService';
import { ConfService } from '../services/confService';
import { AccessService } from '../services/accessService';

export interface Services {
    supervisorService: SupervisorService;
    licenserService: LicenserService;
    confService: ConfService;
    accessService: AccessService;
}

export const ServicesContext = createContext<Services | null>(null);

export const ServiceProvider: FC = ({ children }) => {
    const services: Services = useMemo(() => createHttpServices(), []);

    return <ServicesContext.Provider value={services}>{children}</ServicesContext.Provider>;
};

export const useServices = (): Services => {
    const services = useContext(ServicesContext);
    if (!services) {
        throw new Error('useServices must be called within ServiceProvider');
    }
    return services;
};
