import Message from '@splunk/react-ui/Message';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import { RemoteProps } from '@splunk/splunk-assist-remote-api';
import { variables } from '@splunk/themes';
import React, { FC, lazy, Suspense, useCallback, useState } from 'react';
import styled from 'styled-components';
import {
    AssistPendingActivation,
    getPendingActivationState,
} from '../../onboarding/components/AssistPendingActivation/AssistPendingActivation';
import { ActivationFailed } from '../../onboarding/components/ActivationFailed';
import { isLoadingState } from '../hooks/useLoadAsync';
import { AssistStatuses, useAssist } from '../providers/Assist/AssistContext';
import { invalidTenantStatuses, TenantStatus } from '../types';
import Center from './Center';
import { PendingActivationState } from '../../onboarding/components/AssistPendingActivation/AssistPendingDetails';
import {
    ONBOARDING_STATE_PENDING,
    ONBOARDING_STATE_COMPLETED,
} from '../../onboarding/common/OnboardingState';

const AssistEnabledRoutes = lazy(() => import('../routing/AssistEnabledRoutes'));

const AssistDisabledRoutes = lazy(() => import('../routing/AssistDisabledRoutes'));

const Loader: FC = () => (
    <Center data-test="loader">
        <WaitSpinner size="large" />
    </Center>
);

/* This message is rendered within Wrapper.tsx
   Wrapper.tsx applies a padding for the entire app. 
   We need the message to stretch the full width of the screen 
   (outside its parent container). 
*/
const GlobalMessage = styled(Message)`
    margin-left: -${variables.spacingLarge};
    margin-right: -${variables.spacingLarge};
    margin-top: -${variables.spacingXSmall};
    background-color: ${variables.backgroundColorFloating};
`;

const AppContainer = styled.div`
    overflow: auto;
`;

function isPending(optinStatus: AssistStatuses, tenantStatus: TenantStatus, assistEnabled: boolean): boolean {
    const state = getPendingActivationState(optinStatus, tenantStatus, assistEnabled);
    return state !== PendingActivationState.COMPLETED;
}

// sessionStorage only accepts strings
const GLOBAL_MESSAGE_HIDDEN = 'false';
const GLOBAL_MESSAGE_VISIBLE = 'true';
const GLOBAL_MESSAGE_STATUS_KEY = 'showGlobalMessage';

export const ONBOARD_STATE_LOCAL_STORAGE_KEY = 'onboardingStateLocalStorageKey';

export const AssistApp: FC<RemoteProps> = ({ context, isTest }) => {
    const { optinStatus, loadingState, reload, tenantStatus, assistEnabled } = useAssist();
    const [globalMessageStatus, setGlobalMessageStatus] = useState<string>(
        sessionStorage.getItem(GLOBAL_MESSAGE_STATUS_KEY) || GLOBAL_MESSAGE_VISIBLE
    );
    const onRemoveGlobalMessage = useCallback(() => {
        sessionStorage.setItem(GLOBAL_MESSAGE_STATUS_KEY, GLOBAL_MESSAGE_HIDDEN);
        setGlobalMessageStatus(GLOBAL_MESSAGE_HIDDEN);
    }, []);

    const [onboardingState, setOnboardingState] = useState<string>(
        sessionStorage.getItem(ONBOARD_STATE_LOCAL_STORAGE_KEY) || ONBOARDING_STATE_PENDING
    );

    const onUpdateOnboardingState = (state: string): void => {
        sessionStorage.setItem(ONBOARD_STATE_LOCAL_STORAGE_KEY, state);
        setOnboardingState(state);
    };

    if (isLoadingState(loadingState)) {
        return <Loader />;
    }

    if (invalidTenantStatuses.includes(tenantStatus)) {
        return <ActivationFailed />;
    }

    if (
        (!assistEnabled || optinStatus === AssistStatuses.DISABLED) &&
        onboardingState !== ONBOARDING_STATE_COMPLETED
    ) {
        return (
            <Suspense fallback={<Loader />}>
                <AssistDisabledRoutes
                    context={context}
                    isTest={isTest}
                    onboardingState={onboardingState}
                    onUpdateOnboarding={onUpdateOnboardingState}
                />
            </Suspense>
        );
    }

    if (isPending(optinStatus, tenantStatus, assistEnabled)) {
        return (
            <Center>
                <AssistPendingActivation reloadAssistContext={reload} />
            </Center>
        );
    }

    return (
        <Suspense fallback={<Loader />}>
            {globalMessageStatus === GLOBAL_MESSAGE_VISIBLE && (
                <GlobalMessage
                    data-test="global-message"
                    appearance="banner"
                    onRequestRemove={onRemoveGlobalMessage}
                    type="info"
                >
                    Indicator updates can take up to 1 hour to appear on this page.
                </GlobalMessage>
            )}
            <AppContainer>
                <AssistEnabledRoutes context={context} isTest={isTest} />
            </AppContainer>
        </Suspense>
    );
};
