import { RemoteProps } from '@splunk/splunk-assist-remote-api';
import React, { FC } from 'react';
import { HashRouter, useHistory } from 'react-router-dom';
import { AssistApp } from '../common/components/AssistApp';
import { Wrapper } from '../common/components/Wrapper';
import { AssistProvider } from '../common/providers/Assist/AssistProvider';
import { NavUtilsProvider } from '../common/providers/NavUtilsProvider';
import { RepoProvider } from '../common/providers/RepoProvider';
import { ServiceProvider } from '../common/providers/ServiceProvider';
import { loadTenant } from '../common/utils/tenant';
import { TelemetryProvider } from '../../telemetry/TelemetryProvider';

export const AssistRoutes: FC<RemoteProps> = ({ context, isTest }) => {
    const history = useHistory();

    // Set shouldUseSCS to true below to send requests directly to SCS Playground for testing
    return (
        <ServiceProvider>
            <RepoProvider testFlags={{ shouldUseSCS: false, tenant: loadTenant(context.isSE) }}>
                <NavUtilsProvider history={history}>
                    <Wrapper colorScheme="dark">
                        <TelemetryProvider disableTelemetry={isTest}>
                            <AssistProvider>
                                <AssistApp context={context} isTest={isTest} />
                            </AssistProvider>
                        </TelemetryProvider>
                    </Wrapper>
                </NavUtilsProvider>
            </RepoProvider>
        </ServiceProvider>
    );
};

export const AssistRouter: FC<RemoteProps> = (props) => {
    return (
        <HashRouter>
            <AssistRoutes {...props} />
        </HashRouter>
    );
};

export default AssistRouter;
