import React, { FC, useCallback, useEffect, useRef, useState } from 'react';
import { useErrorHandler } from 'react-error-boundary';
import { listenForDOMEvents, setupTelemetry } from './utils';
import { useLoadAsync } from '../ui/common/hooks/useLoadAsync';
import { useServices } from '../ui/common/providers/ServiceProvider';
import { TelemetryContextProvider } from './TelemetryContext';

interface TelemetryProviderProps {
    /**
     * if disableTelemetry is set, telemetry will not be collected
     */
    disableTelemetry: boolean;
}

export const TelemetryProvider: FC<TelemetryProviderProps> = ({ disableTelemetry, children }) => {
    const { confService, accessService } = useServices();
    // state used as flag to only call listenForDOMEvents and telemetry.identify once
    const [called, setCalled] = useState(false);
    const providerDOM = useRef<HTMLDivElement>(null);

    // Access API call
    const handleError = useErrorHandler();
    const userContext = useLoadAsync(
        useCallback(() => {
            return (): void => {};
        }, []),
        'Error loading current context',
        useCallback(() => {
            return accessService.getCurrentContext();
        }, [accessService])
    );

    // Conf API call
    const assistEnv = useLoadAsync(
        handleError,
        'Error loading assist environment info',
        useCallback(() => {
            return confService.getAssistEnv();
        }, [confService])
    );

    const telemetry = disableTelemetry ? undefined : setupTelemetry(assistEnv.value || '');
    useEffect(() => {
        if (telemetry && !called && userContext.value?.email) {
            telemetry.identify({
                // pass in window.location.host in order to obfuscate the IP address
                tenant: window.location.host,
                user: userContext.value.email,
            });
            listenForDOMEvents(telemetry, providerDOM.current);
            setCalled(true);
        }
    }, [telemetry, userContext, setCalled, called]);

    return (
        <TelemetryContextProvider value={{ telemetry }}>
            <div ref={providerDOM}>{children}</div>
        </TelemetryContextProvider>
    );
};
