import reduce from 'lodash/reduce';
import { APIClient } from '../api/APIClient';
import { LicenserService } from '../../ui/common/services/licencerService';

/* eslint-disable camelcase */
export function createHTTPLicenserService(apiClient: APIClient): LicenserService {
    return {
        getLicenseGUID: async () => {
            try {
                const response = await apiClient.get('getLicenseInfo', {
                    method: 'GET',
                    path: 'licenser/localslave',
                    queryParams: {
                        output_mode: 'json',
                    },
                });
                const { content } = response.entry[0];

                // retrieve addon license guids
                const addonGuids = reduce(
                    content.add_ons,
                    (guids, addon) => {
                        if (addon?.parameters?.guid) {
                            guids.push(addon.parameters.guid);
                        }
                        return guids;
                    },
                    [] as string[]
                );

                // filter out add on licenses
                const licenseGUID = content.guid.filter((guid) => !addonGuids.includes(guid));

                // return the first valid license in the array
                return licenseGUID[0].toLowerCase();
            } catch {
                return null;
            }
        },
    } as LicenserService;
}
