import { ConfService } from '../../ui/common/services/confService';
import { APIClient } from '../api/APIClient';

export function createHTTPConfService(apiClient: APIClient): ConfService {
    return {
        getAssistEnv: async () => {
            try {
                const response = await apiClient.get('getAssistEnv', {
                    method: 'GET',
                    path: 'properties/assist/cloud',
                    queryParams: {
                        output_mode: 'json',
                    },
                });
                const scsEnv = response.entry.find((value) => value.name === 'scs_environment');
                return scsEnv.content;
            } catch (e) {
                return 'production';
            }
        },
    } as ConfService;
}
