export interface PendingActivationDetails {
    title: string;
    message: string;
    altMessage?: string;
}

export enum PendingActivationState {
    INITIAL,
    OPT_IN,
    TENANT_CREATING,
    COMPLETED,
}

// This will show for the title of every message.
const pendingActivationTitle = 'Splunk Assist is loading';
const activatingAssistTitle = 'Activating Splunk Assist...';
/**
 * These are the rotating messages that appear to the user while
 * waiting for tenant_status to go from "unknown" to "creating"
 * */
const activatingAssistDetails: PendingActivationDetails = {
    title: pendingActivationTitle,
    message: activatingAssistTitle,
    altMessage: 'This could take a few moments, stay tuned...',
};
const connectingCloudServicesDetails: PendingActivationDetails = {
    title: pendingActivationTitle,
    message: 'Connecting to Splunk cloud servicesâ€¦',
};
const onboardingRequestDetails: PendingActivationDetails = {
    title: pendingActivationTitle,
    message: 'Connected to Splunk cloud services.',
    altMessage: 'Sending onboarding request...',
};
const securingConnectionsDetails: PendingActivationDetails = {
    title: pendingActivationTitle,
    message: 'Establishing secure connections...',
};

export const optInActivationDetails = [
    activatingAssistDetails,
    connectingCloudServicesDetails,
    onboardingRequestDetails,
    securingConnectionsDetails,
];

// This function handles the request to get the next optIn details message
export function getNextOptInActivationDetails(index: number): PendingActivationDetails {
    if (index >= optInActivationDetails.length) {
        return securingConnectionsDetails;
    }
    return optInActivationDetails[index];
}

// Function that returns each pending activation message for the UI
// based on the activation state.
export function getPendingActivationDetails(
    state: PendingActivationState,
    index: number
): PendingActivationDetails {
    switch (state) {
        case PendingActivationState.INITIAL:
            return {
                title: pendingActivationTitle,
                message: activatingAssistTitle,
                altMessage: 'This could take a few moments, stay tuned...',
            } as PendingActivationDetails;
        case PendingActivationState.OPT_IN:
            return getNextOptInActivationDetails(index);
        case PendingActivationState.TENANT_CREATING:
            return {
                title: pendingActivationTitle,
                message: 'Secure connections have been established.',
                altMessage: 'Provisioning cloud resources...',
            } as PendingActivationDetails;
        default:
            return {
                title: pendingActivationTitle,
                message: activatingAssistTitle,
                altMessage: 'This could take a few moments, stay tuned',
            } as PendingActivationDetails;
    }
}
