import React, { FC, useCallback, useMemo, useState } from 'react';
import { useErrorHandler } from 'react-error-boundary';
import { useLoadAsync } from '../../hooks/useLoadAsync';
import { useServices } from '../ServiceProvider';
import { AssistContext, AssistStatuses, AssistValue } from './AssistContext';

// Provider hook to create assist value
function useProvideAssist(): AssistValue {
    const handleError = useErrorHandler();
    const {
        supervisorService: { getIsAssistEnabled },
    } = useServices();

    const [lastRefresh, setLastRefresh] = useState(0);

    const { loadingState, value } = useLoadAsync(
        handleError,
        'Error verifying Splunk Assist is enabled',
        getIsAssistEnabled,
        { alwaysReset: false, lastRefresh }
    );
    const reload = useCallback((): void => {
        setLastRefresh((old) => (old < Number.MAX_SAFE_INTEGER - 10 ? old + 1 : 0));
    }, []);

    const assistValue: AssistValue = useMemo(
        () => ({
            connectivityOpen: value?.connectivityOpen || false,
            loadingState,
            reload,
            optinStatus: value?.optinStatus || AssistStatuses.UNKNOWN,
            tenantStatus: value?.tenantStatus || 'unknown',
            assistEnabled: value?.assistEnabled || false,
            sudOptinStatus: value?.sudOptinStatus || false,
        }),
        [
            value?.connectivityOpen,
            value?.optinStatus,
            value?.tenantStatus,
            loadingState,
            reload,
            value?.assistEnabled,
            value?.sudOptinStatus,
        ]
    );

    return assistValue;
}

export const AssistProvider: FC = ({ children }) => {
    const assist = useProvideAssist();
    return <AssistContext.Provider value={assist}>{children}</AssistContext.Provider>;
};
