import { createTelemetry, Telemetry } from '@splunk/skinny-web-telemetry';
import { createSplunkOtelPlugin } from '@splunk/skinny-web-opentelemetry-plugin';
import { useMemo } from 'react';

enum ASSIST_ENV_TO_TELEMETRY_ENV {
    playground = 'play',
    production = 'prod',
    staging = 'stage',
}

export const setupTelemetry = (assistEnv: string): Telemetry | undefined => {
    /* eslint-disable react-hooks/rules-of-hooks */
    const memoizedTelemetry = useMemo(() => {
        const env = ASSIST_ENV_TO_TELEMETRY_ENV[assistEnv];
        if (env === undefined) {
            return undefined;
        }

        const telemetry = createTelemetry();

        const splunkOtelPlugin = createSplunkOtelPlugin({
            // the current appType is set to 'ec' as that is the only type that supports Assist (which is an on-prem app)
            // as of now. Skinny web telemetry is planning to create a new appType for on-prem apps as a part of their roadmap
            appType: 'ec',
            appName: 'splunk-assist-telemetry',
            environment: env,
            shouldRemovePII: true,
            shouldDisableAutocapture: true,
            shouldRemoveQueryParams: true,
        });
        telemetry.addPlugin(splunkOtelPlugin);

        return telemetry;
    }, [assistEnv]);

    return memoizedTelemetry;
};

export const captureDOMEvent = (targetElement: HTMLElement | null, telemetry?: Telemetry): void => {
    let dataFound = '';
    let currentElem = targetElement;
    while (currentElem) {
        if (currentElem.dataset.telemetryData) {
            dataFound = currentElem.dataset.telemetryData;
            break;
        }
        currentElem = currentElem?.parentElement;
    }

    if (dataFound) {
        const { eventName, ...attributes } = JSON.parse(dataFound);
        if (!eventName) {
            return;
        }
        telemetry?.track(eventName, attributes);
    }
};

export const listenForDOMEvents = (telemetry: Telemetry, element: HTMLElement | null): void => {
    // add listener for all onclick dom events
    const elementToListen = element || document;
    elementToListen.addEventListener('click', (e) => captureDOMEvent(e.target as HTMLElement, telemetry));

    // in the future, we may want to add telemetry to onchange events. Debouncing is not needed
    // for textfields as DOM onchange events only trigger when the user focuses off the element
    // document.addEventListener('change', (e) => captureDOMEvent(e.target as HTMLElement));
};

export default {
    setupTelemetry,
    captureDOMEvent,
    listenForDOMEvents,
};
